import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useVideo } from '../contexts/VideoContext';
import VideoCard from '../components/VideoCard';
import CategoryTabs from '../components/CategoryTabs';
import Leaderboard from '../components/Leaderboard';
import ChatBubble from '../components/ChatBubble';
import logo from '../img/logo.png';

const Home = () => {
  const { user, isAuthenticated } = useAuth();
  const { 
    videos, 
    categories, 
    currentCategory, 
    loading, 
    error, 
    leaderboard,
    loadVideosByCategory,
    loadFeaturedVideos,
    clearError 
  } = useVideo();
  
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState('');
  const [activeSubcategory, setActiveSubcategory] = useState('pre-k-2');

  // Fallback categories if the API fails
  const fallbackCategories = [
    { id: 'workout-sports', name: 'Workout & Sports', color: '#FF6B35' },
    { id: 'dance-move', name: 'Dance & Move', color: '#4ECDC4' },
    { id: 'yoga', name: 'Yoga', color: '#45B7D1' },
    { id: 'mindfulness', name: 'Mindfulness', color: '#96CEB4' }
  ];

  // Use fallback categories if the API categories are empty
  const displayCategories = categories && categories.length > 0 ? categories : fallbackCategories;

  useEffect(() => {
    // Always load featured videos on mount
    loadFeaturedVideos();
  }, []);

  const handleCategoryChange = (category) => {
    if (category === 'featured') {
      loadFeaturedVideos();
      // Also need to update the current category in the context
      // This will be handled by the loadFeaturedVideos function
    } else {
      loadVideosByCategory(category);
    }
  };

  const handleVideoClick = (videoId) => {
    navigate(`/video/${videoId}`);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      // This would be implemented in the video context
      console.log('Search:', searchQuery);
    }
  };

  const handleSubcategoryChange = (subcategory) => {
    setActiveSubcategory(subcategory);
    // Filter videos based on subcategory
    console.log('Subcategory changed to:', subcategory);
  };

  if (error) {
    return (
      <div className="error">
        {error}
        <button onClick={clearError} className="btn btn-primary" style={{ marginLeft: '1rem' }}>
          Try Again
        </button>
      </div>
    );
  }

  return (
    <div className="fade-in">
      {/* Header with branding */}
      <div style={{ textAlign: 'center', marginBottom: '3rem' }}>
        <img 
          src={logo} 
          alt="Wellness in Schools Logo" 
          style={{ 
            height: '120px', 
            width: 'auto',
            marginBottom: '1rem',
            filter: 'drop-shadow(0 4px 8px rgba(0,0,0,0.4))'
          }} 
        />
        
        {/* Pencil underline */}
        <div style={{
          width: '200px',
          height: '8px',
          background: 'linear-gradient(90deg, #D2B48C 0%, #F4A460 50%, #DEB887 100%)',
          margin: '1rem auto',
          borderRadius: '4px',
          position: 'relative'
        }}>
          <div style={{
            width: '20px',
            height: '12px',
            background: 'linear-gradient(45deg, #D2B48C, #F4A460)',
            position: 'absolute',
            right: '-10px',
            top: '-2px',
            borderRadius: '0 50% 50% 0'
          }}></div>
        </div>
      </div>

      {/* Category Tabs */}
      <CategoryTabs 
        categories={displayCategories}
        currentCategory={currentCategory}
        onCategoryChange={handleCategoryChange}
        activeSubcategory={activeSubcategory}
        onSubcategoryChange={handleSubcategoryChange}
      />

      {/* Search Bar */}
      <div style={{ textAlign: 'center', marginBottom: '2rem' }}>
        <form onSubmit={handleSearch} style={{ display: 'inline-block' }}>
          <input
            type="text"
            placeholder="Search videos..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="form-input"
            style={{ width: '300px', marginRight: '1rem' }}
          />
          <button type="submit" className="btn btn-secondary">
            Search
          </button>
        </form>
      </div>

      {/* Main Content */}
      <div style={{ display: 'grid', gridTemplateColumns: '1fr 300px', gap: '2rem' }}>
        {/* Video Section */}
        <div>
          <h2>Videos of the Week!</h2>
          
          {loading ? (
            <div className="loading">Loading videos...</div>
          ) : videos.length > 0 ? (
            <div className="video-grid">
              {videos.map((video) => (
                <VideoCard
                  key={video._id}
                  video={video}
                  onClick={() => handleVideoClick(video._id)}
                />
              ))}
            </div>
          ) : (
            <div style={{ textAlign: 'center', padding: '2rem' }}>
              <p>No videos found for this category.</p>
            </div>
          )}
        </div>

        {/* Sidebar */}
        <div>
          {/* Leaderboard */}
          <Leaderboard leaderboard={leaderboard} />
          
          {/* User Stats (if authenticated) */}
          {isAuthenticated && user && (
            <div className="card">
              <h3>Your Stats</h3>
              <p>Total Views: {user.totalViews || 0}</p>
              <p>School: {user.schoolName}</p>
            </div>
          )}
        </div>
      </div>

      {/* Footer */}
      <div style={{ textAlign: 'center', marginTop: '3rem', padding: '2rem' }}>
        <h3>WELLNESS IN THE SCHOOLS</h3>
        <div style={{ display: 'flex', justifyContent: 'center', gap: '1rem' }}>
          <img 
            src={logo} 
            alt="Wellness in Schools Logo" 
            style={{ 
              height: '40px', 
              width: 'auto',
              filter: 'drop-shadow(0 2px 4px rgba(0,0,0,0.3))'
            }} 
          />
        </div>
      </div>

      {/* Chat Bubble */}
      <ChatBubble />
    </div>
  );
};

export default Home; 